











































































































import { Component, Vue } from 'vue-property-decorator'
import { getQueryCourses, changeState } from '@/api/course'
import { Form } from 'element-ui'

@Component
export default class CourseList extends Vue {
  private filterParams: any = {
    currentPage: 1,
    pageSize: 10,
    courseName: '',
    status: ''
  }

  private courses: any[] = []
  private totalCount = 0
  private loading = true

  private created () {
    this.loadCourses()
  }

  private async loadCourses () {
    this.loading = true
    const { data } = await getQueryCourses(this.filterParams)
    data.data.records.forEach((item: any) => {
      item.isStatusLoading = false
    })
    this.courses = data.data.records
    this.totalCount = data.data.total
    this.loading = false
  }

  private handleCurrentChange (page: number) {
    this.filterParams.currentPage = page
    this.loadCourses()
  }

  private handleFilter () {
    this.filterParams.currentPage = 1
    this.loadCourses()
  }

  private handleReset () {
    (this.$refs.form as Form).resetFields()
    this.filterParams.currentPage = 1
    this.loadCourses()
  }

  private async onStateChange (course: any) {
    course.isStatusLoading = true
    await changeState({
      courseId: course.id,
      status: course.status
    })
    this.$message.success(`${course.status === 0 ? '下架' : '上架'}成功`)
    course.isStatusLoading = false
  }
}
